@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Roboto+Slab&display=swap');


/* ----------------------------------------------------------------------------------------------
#SECTION-TAGS
---------------------------------------------------------------------------------------------- */

body{
  background-color:#B3ECF2;
  overflow-x: hidden;
}

a{
  color:azure;
  text-decoration: none;
}

select{
  margin-right: 15px;
  max-width: 250px;
}

form{
  display: flex;
  flex-direction: row;
}

option{
  overflow: auto;
}

label{
  margin-right: 15px;
  width: fit-content;
}

input{
  height:22px;
  margin-right: 70px;
  font-family:'Roboto Slab', serif;
  font-size: 17px;
  text-align: center;
}

input[type="number"]::-webkit-outer-spin-button,input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
   margin: 0;
}

section{
  padding:0 0 0 15px;
  margin: 0 100px 0 0;
}

th {
  display: table-cell;
  vertical-align: inherit;
  font-size: 15px;
  font-weight: bold;
  text-align: -internal-center;
  background-color: #fff;
}




/* ---------------------------------------------------------------------------------------------------
#SECTION-WEBKIT
--------------------------------------------------------------------------------------------------- */

::-webkit-scrollbar{
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track{
  background-color: #ccc;
}

::-webkit-scrollbar-button:vertical:decrement{
  background-color:#404A4F;
}

/* ::-webkit-scrollbar-button:vertical:increment{
  background-color:#404A4F;
  border-radius: 0 0 25px 0;
} */

::-webkit-scrollbar-thumb{
  background-color:#999;
}




/* ---------------------------------------------------------------------------------------------------
#SECTION-HEADER
--------------------------------------------------------------------------------------------------- */

#header{
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: end;
  align-items: center;
  align-content: center;
  color:azure;
  background-color: #365359;
  border: #365359 1px solid;
  font-family: 'Lobster', cursive;
  font-size:20px;
  border-bottom: 1px solid #E0DEDF;
}

#header img{
  display: none;
  width:8%;
  height:90%;
}

#header p{
  margin: 10px;
}





/*-------------------------------------------------------------------------------------------------------
#SECTION-SIDE-MENU
------------------------------------------------------------------------------------------------------- */

#side-menu{
  background: linear-gradient(to bottom,#365359,#B3ECF2); 
  color:#365359;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  align-content:center;
  width: 350px;
  height:540px;
}

#side-menu a{
  display: flex;
  align-items: center;
  width:fit-content;
  height:fit-content;
}

#side-menu__btn{
  width:100%;
  height:60%;
  color:#eee;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 15px;
  font-family: 'Roboto Slab', serif;
  font-size: 20px;
  transition: all 0.3s;
  margin: 30px 0 30px 0;
}

#side-menu__btn:hover{
  background-color: #E0DEDF;
  color:#365359;
  cursor:pointer;
}





/* ------------------------------------------------------------------------------------------------------------
#SECTION-SEARCH
------------------------------------------------------------------------------------------------------------ */

#search{
  display: flex;
  flex-direction: row;
  align-items:center;
  justify-items: center;
  width: 250px;
  justify-content: center;
}

#search a{
  height:fit-content;
}

#search__input{
  margin:0;
  width:250px;
  height: fit-content;
  border-radius: 25px;
  margin-right: 5px;
  font-size: small;
  text-align: left;
  padding-left: 8px;
}

#search__btn{
  border-radius: 25px;
  background-color: beige;
}





/* ------------------------------------------------------------------------------------------------------------
#SECTION-OUTCOMES
------------------------------------------------------------------------------------------------------------ */

.outcomes{
  display: none;
  margin: 70px 0px 0 0;
  height: fit-content;
  max-height: 300px;
  width: 309px;
  border:none;
  overflow: auto;
  background-color: #D8D7D4;
  border-radius: 3px;
}

.outcomes__element{
  display:none;
  justify-content: center;
  align-items: center;
  background-color: #073A40;
  color:#fff;
  border-radius: 25px;
  border:none;
  width:125px;
  padding:5px;
  margin: 10px 10px;
  height: fit-content;
  box-sizing: border-box;
  font-size:15px;
}

.outcomes__element:hover{
  cursor:pointer;
}

.outcomes--show{
  display: flex !important;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row; 
}

.outcomes__element--show{
  display: flex !important;
  flex-wrap:wrap;
}

.outcomes--hidden{
  display:none;
}





/* ----------------------------------------------------------------------------------------------------------
#SECTION-BUDGET
---------------------------------------------------------------------------------------------------------- */

#budget__element{
  position: absolute;
  left: 28%;
  top:13%;
  display: flex;
  width: 878px;
  flex-direction: row;
  padding: 15px;
  justify-content: space-around;
  align-items: center;
  height: fit-content;
  background-color: #B3ECF2;
  color: black;
  font-family: 'Roboto Slab', serif;
  font-size: 20px;
}

#budget__add{
  position: absolute;
  left: 99%;
  top: -20.2%;
  margin: 25px 0 20px 0;
  height: fit-content;
  width:3%;
  height: fit-content;
  color: #3B7302;
  font-family:'Roboto Slab', serif;
  font-size: 20px;
}

.budget__chart{
  position: absolute;
  left:28%;
  top: 25%;
  display:none;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  width:70%;
  height:fit-content;
}

#budget__clear-btn{
  border-radius: 25px;
  background-color: beige;
  height: 31px;
  font-family: 'Roboto Slab', serif;
  width: 245px;
  font-size: 19px;
  margin: 25px 0 25px 0;
}

.budget__chart--show{ 
  display: flex;
}

.budget__new-lines{
  text-align: center;
  padding: 0;
}

.budget__element--show{
  display: table-row-group !important;
}

.budget__element--hidden{
  display:none;
}

.budget__element--light{
  font-size: 18px;
  font-weight: 400;
}





/* ------------------------------------------------------------------------------------------------------------
#SECTION-CARD
------------------------------------------------------------------------------------------------------------ */

.card{
  position: absolute;
  left:28%;
  top:16%;
  background-color:white;
  font-family: 'Roboto Slab', serif;
  border-radius: 15%;
  padding:10px;
}

.card__not-found{
  position: absolute;
  width:fit-content;
  left:28%;
  top:16%;
  background-color:white;
  font-family: 'Roboto Slab', serif;
  border-radius: 25%;
  padding:10px;
  margin:0;
  z-index: -1;
}

.card--hidden{
  display:none;
}













